import React from 'react';
import Header from "../../UI/header/Header"

import {Col, Container,Row} from "react-bootstrap"

import FilterMovie from '../../containers/filterMovie/FilterMovie'
import Pagination from "../../compontents/Pagination/Pagination"
import {useDispatch, useSelector} from "react-redux"
import {setPage} from "../../redux/slice/filterSlice"
import Sort from "../../compontents/sort/Sort"


const PageFilters = () => {


    const totalPage = useSelector(state => state.filter.totalPage);
    const dispatch = useDispatch()
    const onPageChange = (event) => {
        dispatch(setPage(event.selected))
        window.scrollTo(0, 0)
    }

    return (
        <>


            <Header search/>

            <Container>
                <Row>
                    <Col xl={3}>
                        <Sort/>
                    </Col>


                    <Col xl={8}><FilterMovie/></Col>

                </Row>


                {totalPage > 1 ? <Pagination onPageChange={onPageChange}
                                             totalPage={totalPage}

                /> : null}
            </Container>


        </>
    );
};

export default PageFilters;