import MoviedbService from '../../services/MoviedbService'


import React, {useEffect, useState} from 'react';
import {useSelector} from "react-redux"


import SliderMovie from '../../compontents/sliderMovie/SliderMovie'
import SkeletonMovie from '../../compontents/skeletonMovie/SkeletonMovie'
import ErrorMessage from '../../compontents/errorMessage/ErrorMessage'


const moviedbService = new MoviedbService;

const SimilarMovie = () => {

    const movieId = useSelector(state => state.movie.movieId)

    const [movie, setMovie] = useState([])
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState(false)


    useEffect(() => {

        movieUpdate()
    }, [movieId])


    const movieUpdate = () => {

        onMovieLoading();
        moviedbService.getMovieSimilar(movieId).then((res) => {
            onMovieLoaded(res);

        })
            .catch(() => {
                setError(true)
            })
    }

    const onMovieLoading = () => {
        setLoading(true);
    }

    const onMovieLoaded = (res) => {
        setLoading(false)
        setError(false)
        setMovie(res.results)
    }


    const Title = () => {
        return (
            <div className="title">
                Similar movies
            </div>
        )
    }

    const breakpoints = {
        50: {
            slidesPerView: 2,
        },

        400: {
            slidesPerView: 3,
        },
        600: {

            slidesPerView: 5,
        },
        900: {

            slidesPerView: 7,
        },
        1200: {

            slidesPerView: 4,
        },
    }



const content = !(loading || error) ?
    <SliderMovie
        spaceBetween={10}
        breakpoints={breakpoints}
        loading={loading}
        data={movie}/> : null;

const skeleton = loading && !error ? <SkeletonMovie/> : null;
const errorMessage = error ? <ErrorMessage/> : null;


return (


    <div className="similar">
        {!error ? <Title/> : null}
        <div className="similar__slider">
            {skeleton}
            {content}
            {errorMessage}
        </div>


    </div>
)


}
;

export default SimilarMovie;