import React from 'react';
import {Link,} from "react-router-dom";
import {Col, Container, Row} from "react-bootstrap"

import './header.scss'

import Logo from '../../resources/icons/logo/Logo'
import SearchInput from "../../compontents/searchInput/SearchInput"


const Header = ({search}) => {
    return (

        <header className={'header'}>
            <Container>
                <Row className='header__inner align-items-center'>
                    <Col xl='5'>
                        <div className='header__wrap'>
                            <div className='header__wrap-item' >
                                <Link to="/"
                                      className={'header__link'}>
                                    <div className="header__logo">
                                        <Logo className='logo'/>
                                    </div>
                                    <div className="header__title">
                                        Cinema.ua
                                    </div>
                                </Link>
                            </div>
                            <div className='header__wrap-item category'>
                                <Link to='/filters'
                                      className={'header__link'}>
                                    Filters
                                </Link>
                            </div>
                        </div>


                    </Col>

                    {search ? <Col className={'header__search'} xl='5'>

                        <SearchInput/>
                    </Col> : null}

                </Row>

            </Container>
        </header>

    )
        ;
};

export default Header;