import React, { useRef, useState } from "react";

import {Navigation,Scrollbar} from 'swiper';
import {Swiper, SwiperSlide} from 'swiper/react';

import ItemMovie from '../itemMovie/ItemMovie'


import 'swiper/css';
import 'swiper/css/navigation';
import "swiper/css/scrollbar";

import './slider.scss'



const SliderMovie = ({data, loading, spaceBetween,breakpoints }) => {


    let itemsSlide;


    itemsSlide = data.map((item, i) => {

        const {backdrop_path, poster_path, title, name, vote_average, release_date, id} = item;

        if (i > 15) {
            return
        }
        return (

            <SwiperSlide key={id}>
                <ItemMovie
                    name={name}
                    posterImg={poster_path}
                    backdropImg={backdrop_path}
                    title={title}
                    rating={vote_average}
                    ralease={release_date}
                    id={id}
                />
            </SwiperSlide>

        )
    });


    return (


        <Swiper
            breakpoints={breakpoints}
            style={{
                paddingBottom: '30px'
            }}
            scrollbar={{
                hide: false,
                draggable: true,
            }}
            modules={[Navigation,Scrollbar]}
            navigation
            spaceBetween={spaceBetween}
        >

            {itemsSlide}
        </Swiper>


    )


}

export default SliderMovie;