import MoviedbService from '../../services/MoviedbService'

import React, {useEffect, useState} from 'react';
import {useSelector,useDispatch} from "react-redux"

import {setPage,setTotalPage} from "../../redux/slice/searchSlice"


import SkeletonMovie from '../../compontents/skeletonMovie/SkeletonMovie'
import ErrorMessage from '../../compontents/errorMessage/ErrorMessage'
import ListSearch from "../../compontents/listSearch/ListSearch"


const moviedbService = new MoviedbService();

const SeachMovie = () => {

    const search = useSelector(state => state.search.searchValue)
    const page = useSelector(state => state.search.page)
    const totalPage = useSelector(state => state.search.totalPage)

    const [movie, setMovie] = useState([])
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState(false)

    const dispatch = useDispatch()


    useEffect(() => {

        movieUpdate(search)
    }, [search,page,totalPage])




    const onCnangeTotalPage = (value) => {
        dispatch(setTotalPage(value))
    }


    const movieUpdate = () => {

        if (!search) {
            return
        }
        onMovieLoading();
        moviedbService.getSearchMovie(search,page +1).then((res) => {
            onMovieLoaded(res)
            console.log(res)
            onCnangeTotalPage(res.total_pages)

        })
            .catch(() => {
                setError(true)
            })
    }

    const onMovieLoading = () => {
        setLoading(true);
    }

    const onMovieLoaded = (res) => {
        setLoading(false)
        setError(false)
        setMovie(res.results)
    }

    const NoResults = () => {
        return(
            <div style={{color: 'white'}} className='mt-2 p-xl-3'>
                No results
            </div>
        )
    }

    const content = !(loading || error || !movie) ?  <ListSearch data={movie}/> : null;

    const skeleton = loading && !error ? <SkeletonMovie height={382}/> : null;
    const errorMessage = error ? <ErrorMessage/> : null;





    return (
        <>


            {skeleton}
            {content}
            {errorMessage}


        </>

    )


};

export default SeachMovie;