import React from 'react';
import ItemSearch from "../itemSearch/ItemSearch"
import {Col, Container, Row} from "react-bootstrap"
import SkeletonMoivie from "../listMovie/ListMovie"




const ListSearch = ({data,loading }) => {


    const itemsMovie = data.map((item, i) => {

        const {backdrop_path, poster_path, title, name, vote_average, release_date, id,overview} = item;

        if (i > 5) {
            return
        }
        return <Col xl="12" className='align-items-stretch' style={{marginTop: '25px'}}
                    key={id}
        >

            {loading || !data ? <SkeletonMoivie/> :
                <ItemSearch
                    name={name}
                    posterImg={poster_path}
                    backdropImg={backdrop_path}
                    title={title}
                    rating={vote_average}
                    ralease={release_date}
                    id={id}
                    overview={overview}

                />}
        </Col>;
    });




    return (


            <Row style={{alignItems: 'flex start'}}>
                {itemsMovie}
            </Row>



    )
}

export default ListSearch;