import MoviedbService from '../../services/MoviedbService'


import React, {useEffect, useState} from 'react';
import {useSelector} from "react-redux"


import SkeletonMovie from '../../compontents/skeletonMovie/SkeletonMovie'
import MovieInfoView from '../../UI/movieInfoView/MovieInfoView'
import ErrorMessage from '../../compontents/errorMessage/ErrorMessage'


const moviedbService = new MoviedbService;


const InfoMovie = () => {

    const movieId = useSelector(state => state.movie.movieId)



    const [movie, setMovie] = useState({})
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState(false)


    useEffect(() => {

        movieUpdate(movieId)
    }, [movieId])


    const movieUpdate = () => {

        if (!movieId) {
            return
        }
        onMovieLoading();
        moviedbService.getMovie(movieId).then((res) => {
            onMovieLoaded(res)
        })
            .catch(() => {
                setError(true)
            })
    }

    const onMovieLoading = () => {
        setLoading(true);
    }

    const onMovieLoaded = (res) => {
        setLoading(false)
        setError(false)
        setMovie(res)


    }

    const {backdrop_path, poster_path, title, name, vote_average, release_date, id, genres, budget, overview} = movie;

    const content = !(loading || error || !movie)  ? <MovieInfoView
        name={name}
        posterImg={poster_path}
        backdropImg={backdrop_path}
        title={title}
        rating={vote_average}
        ralease={release_date}
        genres={genres}
        budget={budget}
        overview={overview}
        id={id}
    /> : null;

    const skeleton =   loading && !error  ?   <SkeletonMovie height={382} /> :  null ;
    const errorMessage = error ? <ErrorMessage/> : null;


    return (
        <>
            {skeleton}
            {content}
            {errorMessage}

        </>

    )
}

export default InfoMovie