import TrendingMovie from '../../containers/trendingMovie/TrengingMovie'
import InfoMovie from '../../containers/infoMovie/InfoMovie'
import SimilarMovie from '../../containers/similarMovie/SimilarMovie'


import {Col, Container, Row} from 'react-bootstrap'
import Header from "../../UI/header/Header"


const Home = () => {

    return (
        <>
            <Header search/>
            <Container style={{maxWidth: '1200px'}} fluid>
                <Row>
                    <Col xl={12}>
                        <TrendingMovie/>
                    </Col>

                </Row>
                <Row sm='justify-content-center' xl='justify-content-between'>
                    <Col xl={7}>
                        <InfoMovie/>
                    </Col>
                    <Col className="mt-4" xl={5}>
                        <SimilarMovie/>

                    </Col>
                </Row>
            </Container>


        </>
    )
}

export default Home;
