import React from 'react';
import {useDispatch} from "react-redux"
import {setMovieId} from "../../redux/slice/movieSlice"

import {Col, Row} from 'react-bootstrap'

import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import Tooltip from '@mui/material/Tooltip';

import pathImg from '../../feature/pathImg'
import sliceString from "../../feature/sliceString"

import PlugImg from '../../resources/img/plugimg.png'

import styles from './itemSearch.module.scss'
import {Link} from "react-router-dom"
import Button from "../itemMovie/ItemMovie"


const ItemSearch = ({title, name, posterImg, backdropImg, id,overview}) => {

    const dispatch = useDispatch()

    const onChangeMovieId = (id) => {
        dispatch(setMovieId(id))
    }

    const imgMovie = posterImg ? pathImg(posterImg) : backdropImg ? pathImg(backdropImg) : PlugImg;
    const titleMovie = title ? title : name ? name : 'нет имени'


    return (
        <>


            <div
                className={styles.root}
                onClick={() => {
                    onChangeMovieId(id)

                }}
            >

                <Row className={styles.row}>
                    <Col xl={2}>
                        <Link className={styles.link} to={`../movie/${id}`}>
                            <Tooltip arrow placement="right" title="more">
                                <CardMedia
                                    component="img"
                                    className={styles.images}
                                    image={imgMovie}
                                    alt={titleMovie}/>
                            </Tooltip>
                        </Link>
                    </Col>
                    <Col xl={8}>
                        <CardContent className={styles.box}>
                            <Typography
                                variant="h7"
                                component="div"
                                className={styles.title}>
                                {titleMovie}
                            </Typography>

                            <div className="mt-1  text px-3">
                                {overview ? overview : 'no review'}
                            </div>



                        </CardContent>
                    </Col>
                </Row>


            </div>
        </>
    );
};

export default ItemSearch;

