import React from 'react';
import {useSelector,useDispatch} from "react-redux"

import {setPage,setTotalPage} from "../../redux/slice/searchSlice"


import {Col, Container, Row} from 'react-bootstrap'


import Header from "../../UI/header/Header"
import SeachMovie from "../../containers/searchMovie/SeachMovie"


import styles from './pageSearch.module.scss'
import SearchInput from "../../compontents/searchInput/SearchInput"
import Pagination from "../../compontents/Pagination/Pagination"


const PageSearch = () => {

    const totalPage = useSelector(state => state.search.totalPage);
    const dispatch = useDispatch()
    const onPageChange = (event) =>{
        dispatch(setPage(event.selected))
        window.scrollTo(0,0)
    }

    return (
        <>

            <Header/>


            <Container className='mt-4'>

                <Row className={styles.root}>
                    <Col xl={9}>

                       <SearchInput/>
                    </Col>


                    <Col xl={12}>

                        <SeachMovie/>
                        {totalPage > 1 ?  <Pagination onPageChange={onPageChange}
                                                      totalPage={totalPage}

                        /> : null}
                    </Col>



                </Row>

            </Container>


        </>
    );
};

export default PageSearch;